.productspage{
    text-align: center;
}

.products {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
}

.products-title {
    padding-top: 40px;
    padding-bottom: 40px;
    font-family: 'Montserrat', sans-serif;
}

.products img {
    height: 300px;
    width: 250px;
    object-fit: cover;
}


.product {
    padding: 40px;
}

button {
    font-size: 10px;
    padding: 10px;
    font-family: 'Montserrat', sans-serif;
    background-color: #b6b2b2;; 
    border: none;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    

}

.cart-btn {
    padding-right: 10px;
}

.products-btn {
    padding-left: 10px;
}

header {
  
    height: 100px;
    padding-top: 20px;
    padding-bottom: 20px;
}